(function() {
  'use strict';

  angular
    .module('pulld')
    .component('pulldHeader', {
      templateUrl: 'app/shared/header/header.template.html',
      controller: HeaderController,
      controllerAs: 'vm'
    });

  /* @ngInject */
  function HeaderController($window, $scope, $rootScope) {
    var vm = this;
    vm.isSolid = false;
    vm.toggleMenu = toggleMenu;
    vm.isMenu = false;

    function toggleMenu() {
        vm.isMenu = !vm.isMenu;
    }
    angular.element($window).bind("scroll", function() {
      if (this.pageYOffset > angular.element($window).outerHeight() + 20) {

        vm.isSolid = true;

        $scope.$apply();
      } else {
        vm.isSolid = false;

        $scope.$apply();
      }

    });
  }
})();

