(function() {
  'use strict';

  angular
    .module('pulld')
    .component('pulldHomepage', {
      templateUrl: 'app/components/homepage/homepage.template.html',
      controller: HomepageController,
      controllerAs: 'vm'
    });

  /* @ngInject */
  function HomepageController($window) {
      var vm = this;
      vm.scrollPosition = 0;
      vm.scrollHide = 0;
    angular.element($window).bind("scroll", function() {
        vm.scrollBlur = this.pageYOffset / angular.element($window).outerHeight()
        angular.element('.is-overlay').css({'opacity': vm.scrollBlur });
        if(angular.element($window).outerWidth() >= 420) {



         vm.scrollHide = 100 - Math.ceil(this.pageYOffset / (angular.element($window).outerHeight() / 150))


         angular.element('.is-overlay').css({'opacity': vm.scrollBlur });
         if (vm.scrollHide > 10 && vm.scrollHide < 100) {
         angular.element('.first-section').css({'transform': 'translateY(0px) scale(0.'+ vm.scrollHide +')'})
        }
        if (vm.scrollHide < 100) {
            if(vm.scrollHide <= 10) {
                vm.scrollHide = 0;
            }
        angular.element('.first-section').css({'opacity': '0.'+vm.scrollHide })
       }
           }
     });
  }
})();

